import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes} from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import Navbar from './components/NavBar';
import Register from './components/Register';
import SignIn from './components/SignIn';
import Home from './components/Home'; 
import AIResponse from './components/AIResponse';
import Loading from './components/Loading';
import Video from './components/Video';
import Community from "./components/Commnuity";
import About from "./components/About";
import Youtube from './components/Youtube';
import QandA from "./components/QandA";
import Dashboard from "./components/Dashboard";
import RecipeInstruction from './components/RecipeInstruction';

function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(() => {
    const savedLoginState = localStorage.getItem('isLoggedIn');
    return savedLoginState ? JSON.parse(savedLoginState) : false;
  });
  
  const [username, setUsername] = useState(() => {
    return localStorage.getItem('username') || '';
  });

  // Update localStorage whenever login state changes
  useEffect(() => {
    localStorage.setItem('isLoggedIn', JSON.stringify(isLoggedIn));
    localStorage.setItem('username', username);
  }, [isLoggedIn, username]);



  // 
  const handleRegisterSuccess = (newUsername) => {
    setIsLoggedIn(true); //
    setUsername(newUsername); //username RegisteruserName
  };

  // 
  const handleSignInSuccess = (newUsername) => {
    setIsLoggedIn(true); //
    setUsername(newUsername); //username
  };

  // 
  const handleLogout = () => {
    setIsLoggedIn(false); // False
    setUsername('');  // 
    // Clear localStorage on logout
    localStorage.removeItem('isLoggedIn');
    localStorage.removeItem('username');
  };

  return (
    <Router>
      {/* Navbar will always be displayed */}
      <Navbar isLoggedIn={isLoggedIn} username={username}  onLogout={handleLogout} />{/*  & */}
      
      <Routes>
        <Route path="/" element={<Home username={username} />} /> {/* Home  */}
        <Route path="/register" element={<Register onRegisterSuccess={handleRegisterSuccess} />} />  {/* */}
        <Route path="/signin" element={<SignIn onSignInSuccess={handleSignInSuccess} />} /> {/* */}
        <Route path="/loading" element={<Loading />} />
        <Route path="/response" element={<AIResponse isLoggedIn={isLoggedIn} />} />
        <Route path="/video" element={<Video />} />
        <Route path="/community" element={<Community />} />
        <Route path="/about" element={<About />} />
        <Route path="/youtube" element={<Youtube />} />
        <Route path="/qa" element={<QandA />} />
        <Route path="/dashboard" element={<Dashboard />} />
        <Route path="/recipe" element={<RecipeInstruction />} />
      </Routes>
    </Router>
  );
}

export default App;
